<template>
  <v-data-table
      :headers="headers"
      :items="products"
      sort-by="price"
      class="elevation-1"
  >
    <template v-slot:top>
      <v-toolbar
          flat
      >
        <v-toolbar-title>Products List</v-toolbar-title>
        <v-divider
            class="mx-4"
            inset
            vertical
        ></v-divider>
        <v-spacer></v-spacer>
        <v-dialog
            v-model="dialog"
            max-width="500px"
        >
          <template v-slot:activator="{ on, attrs }">
            <v-btn
                color="primary"
                dark
                class="mb-2"
                v-bind="attrs"
                v-on="on"
            >
              New Product
            </v-btn>
          </template>
          <v-card>
            <v-card-title>
              <span class="headline">{{ formTitle }}</span>
            </v-card-title>

            <v-card-text>
              <v-container>
                <v-row>
                  <v-col
                      cols="12"
                      sm="6"
                      md="4"
                  >
                    <v-text-field
                        v-model="editedItem.nom"
                        label="Product name"
                    ></v-text-field>
                  </v-col>
                  <v-col
                      cols="12"
                      sm="6"
                      md="4"
                  >
                    <v-text-field
                        v-model="editedItem.prix"
                        label="Price(DA)"
                    ></v-text-field>
                  </v-col>
                </v-row>
              </v-container>
            </v-card-text>

            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn
                  color="blue darken-1"
                  text
                  @click="close"
              >
                Cancel
              </v-btn>
              <v-btn
                  color="blue darken-1"
                  text
                  @click="save"
              >
                Save
              </v-btn>
            </v-card-actions>
          </v-card>
        </v-dialog>
        <v-dialog v-model="dialogDelete" max-width="500px">
          <v-card>
            <v-card-title class="headline">Are you sure you want to delete this item?</v-card-title>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="blue darken-1" text @click="closeDelete">Cancel</v-btn>
              <v-btn color="blue darken-1" text @click="deleteItemConfirm">OK</v-btn>
              <v-spacer></v-spacer>
            </v-card-actions>
          </v-card>
        </v-dialog>
      </v-toolbar>
    </template>
    <template v-slot:item.actions="{ item }">
      <v-icon
          small
          class="mr-2"
          @click="editItem(item)"
      >
        mdi-pencil
      </v-icon>
      <v-icon
          small
          @click="deleteItem(item)"
      >
        mdi-delete
      </v-icon>
    </template>
    <template v-slot:no-data>
      <v-btn
          color="primary"
          @click="initialize"
      >
        Reset
      </v-btn>
    </template>
  </v-data-table>
</template>

<script>
//import axios from "axios";

export default {
  name: "ProductsTable",
  data: () => ({
    dialog: false,
    dialogDelete: false,
    headers: [
      {
        text: 'Product',
        align: 'start',
        sortable: false,
        value: 'nom',
      },
      {text: 'Price (DA)', value: 'prix'},
      {text: 'ID', value: 'id'},
      {text: 'Actions', value: 'actions', sortable: false},
    ],
    products: [],
    editedIndex: -1,
    editedItem: {
      id: -1,
      nom: '',
      prix: 0,
    },
    defaultItem: {
      id: 0,
      nom: '',
      prix: 0,
    },
  }),
  mounted() {
    /*axios
        .get('http://localhost:8080/Products')
        .then(response => {
          //this.products = response.data;
          console.log(response.status)
        });*/
  },
  computed: {
    formTitle() {
      return this.editedIndex === -1 ? 'New Item' : 'Edit Item'
    },
  },

  watch: {
    dialog(val) {
      val || this.close()
    },
    dialogDelete(val) {
      val || this.closeDelete()
    },
  },

  created() {
    this.initialize();
  },

  methods: {
    initialize() {
      this.getproducts('http://localhost:8080/Products').then((res) => {
            this.products = res
          }
      );
    },

    getproducts(url) {
      return fetch(url)
          .then((response) => response.json())
          .catch(function (err) {
            console.log('Fetch Error :-S', err);
          });
    },

    editItem(item) {
      this.editedIndex = this.products.indexOf(item)
      this.editedItem = Object.assign({}, item)
      this.dialog = true
    },

    deleteItem(item) {
      this.editedIndex = this.products.indexOf(item)
      this.editedItem = Object.assign({}, item)
      this.dialogDelete = true
    },

    deleteItemConfirm() {
      this.deleteProduct(this.editedIndex)
      this.closeDelete()
    },

    close() {
      this.dialog = false
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem)
        this.editedIndex = -1
      })
    },

    closeDelete() {
      this.dialogDelete = false
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem)
        this.editedIndex = -1
      })
    },

    save() {
      if (this.editedIndex > -1) {
        // edit
        //Object.assign(this.products[this.editedIndex], this.editedItem)
        this.saveEditProduct(this.editedItem,'update');
      } else {
        // save
        //this.products.push(this.editedItem)
		this.editedItem.id=0;
        this.saveEditProduct(this.editedItem,'save');
      }
      this.close()
    },

    saveEditProduct(product,operation) {
      fetch('http://localhost:8080/Products/'+operation, {
        method: 'post',
        headers: {
          'Accept': 'application/json',
          'Content-Type': 'application/json'
        },
        body: JSON.stringify(product)
      }).then(()=>{
        this.products=[];
        this.initialize()
      })
          .catch(function (error) {
            console.log('Request failed', error);
          });
    },
    deleteProduct(product) {
      fetch('http://localhost:8080/Products/delete/'+product).
      then(()=>{
        this.products=[];
        this.initialize()
      })
          .catch(function (error) {
            console.log('Request failed', error);
          });
    }
  },
}
</script>

